.project-description{
    overflow-y: auto;
   border: 8px solid hotpink;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {

    background: rgb(255, 128, 192);
    border-radius: 10px;
    
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: pink; 
  }






/* 1281px to higher resolution desktops*/

@media (min-width: 1281px) {  
    .list{
      /* background-color: grey; /*teste*/        
        font-size:  calc(4px + 2vmin);        
        align-items: center;
        justify-content: center;
        padding-top:0.5vw; 
        padding-bottom:0.5vw;     
    }       
  }
  
  /* 1025px to 1280px  */
  
  @media (min-width: 1025px) and (max-width: 1280px) {
   .list{
       /*   background-color: gold; /*teste*/       
        font-size:  calc(2.5px + 1.6vmin);
        align-items: center;
        justify-content: center;
        padding-top:0.5vw; 
        padding-bottom:0.5vw;      
    }    
  }
  
  /*  768px to 1024px  */
  
  @media (min-width: 768px) and (max-width: 1024px) {
   
    .list{
        
        /*   background-color: pink; /*teste*/      
         font-size:  calc(2.2px + 1.5vmin);
          align-items: center;
         justify-content: center;
         padding-top:0.2vw; 
         padding-bottom:0.2vw;  
     
    }
    
  }
  
  /* 768px to 1024px */  
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  
    .list{
        /*  background-color: aliceblue; /*teste*/      
        font-size:  calc(2.6px + 1.5vmin);
         align-items: center;
        justify-content: center;
        padding-top:0.2vw; 
        padding-bottom:0.2vw;  
     
    }
    
  }
  
  /* 481px to 767px  */  
  @media (min-width: 481px) and (max-width: 767px) {
   
    .list{
         /*  background-color: lightgreen; /*teste*/       
         font-size:  calc(2.4px + 1.3vmin);
         align-items: center;
        justify-content: center;
        padding-top:0.2vw; 
        padding-bottom:0.2vw;       
    }

    
  }
  
  /* 320px to 479px  */
  
  @media (min-width: 320px) and (max-width: 480px) {
    
