#default{
    color:  #ffe6f2; /* se calhar faz mais sentido a cor ser assumida pelo link ativo**/
}

.nav-link{
    font-size: 17px;
    transition: color 1s;

}

#toggle-lang-button {
    background-color: transparent;
    border: none;
    font-size: 17px;
    color: rgba(255, 255, 255, 0.5);
    transition: color 1s;

}
#toggle-lang-button:hover{
    color: rgba(255, 255, 255, 1.0);
}

