.App {
  text-align: center;
  background-color: #fffffa; 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: grey;
  font-family: Calibri Light;
}

.App-link {
  color: tomato;
  font-size: 18px;
}




