.container{
    background-color: #fffffa;     
}
.certs{
    border: 0.5px solid #ff4da6 !important;
    text-align: center;
    color: hotpink;  
    cursor: pointer;
    margin: auto;
    margin-bottom: 1em;
    transition: box-shadow 1s;
  }
  .certs:hover{
    box-shadow: 0.1em 0.1em pink;
  }
    
.courses{
    color: hotpink;
    background-color: transparent;
    border: none; 
}
.courses:active, :focus{
    outline: none !important;
    box-shadow: none;
}

/* 1281px to higher resolution desktops*/

@media (min-width: 1281px) {  
    .certs{
      /*  background-color: grey; /*teste*/
        width: 20vw; 
        height: 10vw;    
      
        font-size:  calc(4px + 2vmin);        
        align-items: center;
        justify-content: center;
        padding-top:4vw; 
        padding-bottom:5vw;     
    }
   
    
  }
  
  /* 1025px to 1280px  */
  
  @media (min-width: 1025px) and (max-width: 1280px) {
   
    .certs{
     /*    background-color: gold; /*teste*/
        width: 20vw; 
        height: 10vw;
       
        font-size:  calc(3px + 1.5vmin);
        align-items: center;
        justify-content: center;
        padding-top:4vw; 
        padding-bottom:5vw;  
        
     
    }
 

    
  }
  
  /*  768px to 1024px  */
  
  @media (min-width: 768px) and (max-width: 1024px) {
   
    .certs{
      /*   background-color: lightpink; /*teste*/
        width: 20vw; 
        height: 10vw;

        font-size:  calc(4px + 1.5vmin);      
        align-items: center;
        justify-content: center;
     
    }
    
  }
  
  /* 768px to 1024px */
  
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  
    .certs{
     /*    background-color: aliceblue; /*teste*/
        width: 20vw; 
        height: 10vw;
        
        font-size:  calc(3px + 1.5vmin);
         align-items: center;
        justify-content: center;
        padding-top:3.2vw; 
        padding-bottom:4vw;  
     
    }
    
  }
  
  /* 481px to 767px  */  
  @media (min-width: 481px) and (max-width: 767px) {
   
    .certs{
       /*  background-color: lightgreen; /*teste*/
        width: 20vw; 
        height: 10vw;
    
        font-size:  calc(1.5px + 1.5vmin);      
        align-items: center;
        justify-content: center;
        padding-top:3.5vw; 
        padding-bottom:5vw;       
    }

    
  }
  
  /* 320px to 479px  */
  
  @media (min-width: 320px) and (max-width: 480px) {
    
    .certs{
       /*  background-color: lightsteelblue; /*teste*/
        width: 20vw; 
        height: 10vw;
       
        font-size:  calc(1px + 2vmin); 
        align-items: center;
        justify-content: center;
        padding-top:2.5vw; 
        padding-bottom:5vw;  
     
    }
    
    
  }